.Testimonials{
    display: flex;
    gap: 1rem;
    padding: 0 2rem;
}
.left-t{
    display: flex;
    flex-direction: column;
    gap:2rem;
    color: white;
    flex: 1;
    text-transform: uppercase;
}
.left-t>:nth-child(1){
    font-weight: bold;
    color: var(--orange);
}
.left-t>:nth-child(2),
.left-t>:nth-child(3){
    font-weight: bold;
    font-size:3rem
}
.right-t{
    flex:1;
    position: relative;
}
.right-t>img{
    position: absolute;
    width: 17rem;
    height: 20rem;
    object-fit: cover;
    right: 8rem;
    top: 2rem;
}
.right-t>:nth-child(1){
    position: absolute;
    width: 17rem;
    height: 20rem;
    border: 2px solid orange;
    background-color:transparent;
    right: 9rem;
    top: 0.9rem;
}
.right-t>:nth-child(2){
    position: absolute;
    width: 17rem;
    height: 19rem;
    right: 7rem;
    top: 4rem;
    background: var(--planCard);
}
.arrow{
    display:flex;
    gap: 1rem;
    position: absolute;
    bottom: 1rem;
    left: 3rem;
}
.arrow>img{
    width: 1.5rem;
    cursor: pointer;
}

@media screen and (max-width: 768px){
    .Testimonials{
        flex-direction: column;
    }
    .left-t>:nth-child(1),
    .left-t>:nth-child(2){
        font-size: x-large;
    }

    .right-t{
        display: flex;
        flex-direction: column;
        align-items:center;
        justify-content: center;
        gap: 2rem;
    }
    .right-t>div{
        position: relative;
        display:none;
    }
    .right-t>img{
        top: 0;
        right: 0;
        position: relative;
        align-self: center;
    }
    .right-t>:last-child{
        display: block;
        bottom: 0;
        left: 0;
    }
}